/* ---------------------------------------- Header ---------------------------------------- */
.inner-wrapper {
  width: 1050px;
  margin: 0 auto;
  display: flex;
  align-items: center;
}

.kurly-header {
  width: 100%;
  background: #fff;
  min-height: 156px;
  display: flex; flex-direction: column;
}

/* 1. 최상단 메뉴 */
.top-util {
  font-size: 12px;
  color: #333;
  padding-bottom: 20px;
}
.top-util .inner-wrapper {
  justify-content: flex-end;
  gap: 12px;
  height: 100%;
  color: #ddd;
}
.top-util a { text-decoration: none; color: #333; }
.top-util .purple { color: #5f0080; }

/* 2. 메인 헤더 (로고, 검색창, 아이콘) */
.main-header {
  height: 65px; 
  display: flex;
  align-items: center;
}
.main-header .inner-wrapper {
  justify-content: space-between;
}
.logo-tab-group {
  display: flex;
  align-items: center;
  gap: 20px; /* 로고와 탭 사이 간격 */
}
.logo img { width: 103px; }

.kurly-tabs .tab {
  text-decoration: none;
  color: #b5b5b5; /* 기본은 회색 */
  padding: 0 10px;
  cursor: pointer;
}

.kurly-tabs .tab.active {
  color: #5f0080;
  font-weight: bold;
}


.kurly-tabs .divider {
  color: #eee;
  font-size: 14px;
}

/* 메인헤더 inner-wrapper 정렬 확인 */
.main-header .inner-wrapper {
  justify-content: space-between; /* 요소들을 양 끝으로 분산 */
}

.search-bar {
  display: flex; align-items: center;
  width: 400px; height: 48px;
  border: 1px solid #5f0080;
  border-radius: 6px;
  padding: 0 10px; 
  box-sizing: border-box;
}
.search-bar input {
  flex: 1;
  height: 90%;
  border: none; outline: none;
  font-size: 16px;
}
.search-bar button {
  background: none;
  border: none;
  font-size: 20px;
  color: #5f0080;
  cursor: pointer;
}

.icon-menu {
  display: flex;
  gap: 25px;
  font-size: 23px;
}
.icon-menu a { color: #333; }
.icon-menu a:hover { color: #5f0080; }

/* 3. 내비게이션 */
.nav-bar {
  height: 56px;
  box-shadow: 0 4px 5px rgba(0,0,0,0.05);
}
.nav-bar .inner-wrapper {
  justify-content: space-between;
  height: 100%;
}
.category-menu {
  display: flex;
  align-items: center;
  gap: 10px;

  cursor: pointer;
}
.gnb {
  display: flex;
  gap: 60px;
}
.gnb a { text-decoration: none; color: #333; font-size: 16px; }
.gnb a:hover { color: #5f0080; text-decoration: underline; }

.delivery-info {
  border: 1px solid #eee;
  padding: 5px 12px;
  border-radius: 15px;
  font-size: 13px;
}
.delivery-info a{color: #666;}
.delivery-info span {color: #5f0080; }
.delivery-info:hover {cursor: pointer;}

/* ---------------------------------------- Main ---------------------------------------- */
/* --- 좌우 버튼 --- */
.swiper-button-prev, 
.swiper-button-next {
  padding: 10px;
  background: rgba(51, 51, 51, 0.4);
  border-radius: 50%;

  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
}

.swiper-navigation-icon{color: #f7f7f7;}

.swiper-button-prev::after, 
.swiper-button-next::after {
  font-size: 10px !important; 
  font-weight: bold;
  margin: 0 !important;
  padding: 0 !important;
  line-height: 1 !important;
}


/* --- 메인 컨테이너 및 푸터 가려짐 방지 --- */
.container {
  width: 100%;
  padding-bottom: 100px;
}

/* --- 메인 배너 섹션 --- */
.main-banner {
  width: 100%;
  height: 380px;
  overflow: hidden;
  margin-bottom: 50px;
}

.banner-wrapper {
  width: 100%;
  height: 100%;
  position: relative;
}

.banner-img-area {
  position: relative;
  width: 100%;
  height: 100%;
}

.banner-img-area img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.banner-pagination {
  position: absolute; bottom: 20px; right: 300px; 
  background: rgba(0, 0, 0, 0.4);
  color: #fff;
  padding: 4px 15px;
  border-radius: 20px;
  font-size: 13px;
  z-index: 5;
}
.banner-pagination:hover{cursor: pointer;}

/* --- 상품 섹션 (1050px 중앙 정렬) --- */
.product-section {
  width: 100%;
  display: flex;
  justify-content: center;
}

/* Header.css의 flex 영향을 지우고 세로로 쌓기 위해 설정 */
.product-section .inner-wrapper {
  width: 1050px !important;
  display: flex !important;
  flex-direction: column !important; 
  align-items: center;
  margin: 0 auto;
}

.section-header {
  width: 100%;
  text-align: center;
  margin-bottom: 30px;
}

.section-title {
  display: block;
  font-size: 28px;
  font-weight: 700;
  text-decoration: none;
  color: #333;
}

.section-desc {
  color: #999;
  margin-top: 10px;
  font-size: 16px;
}

/* --- 상품 그리드 및 아이템 --- */
.product-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 18px;
  width: 100%;
}

.product-item {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.img-box {
  position: relative;
  width: 100%;
  height: 320px;
  background: #f4f4f4;
  border-radius: 4px;
  overflow: hidden;
}

.img-box img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.product-item:hover .img-box img {
  transform: scale(1.03);
}

/* 담기 버튼: 이미지 바로 밑에 위치 */
.cart-btn {
  width: 100%;
  height: 45px;
  background: #fff;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  color: #333;
  cursor: pointer;
  margin-top: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 6px;
}

.cart-btn:hover {
  background: #f9f9f9;
}

.badge {
  position: absolute;
  top: 10px;
  left: 10px;
  background: #bd76ff;
  color: #fff;
  padding: 3px 8px;
  font-size: 12px;
  font-weight: bold;
  border-radius: 3px;
  z-index: 2;
}

/* --- 상품 정보 텍스트 --- */
.info-box {
  padding: 12px 0;
}

.info-box .name {
  font-size: 16px;
  color: #333;
  line-height: 1.4;
  margin-bottom: 8px;
  line-clamp: none;
  display: -webkit-box; -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.info-box .origin-price {
  font-size: 14px;
  color: #999;
  text-decoration: line-through;
  margin-bottom: 2px;
}

.info-box .sale-price {
  font-size: 18px;
  font-weight: 800;
  color: #333;
}

.info-box .discount {
  color: #fa622f;
  margin-right: 6px;
}

.info-box .comment {
  font-size: 13px;
  color: #999;
  margin-top: 10px;
}

/* ---------------------------------------- Footer ---------------------------------------- */
footer{
  position: relative;
  width: 100%;
  z-index: 100;
}

/* Footer 전체 컨테이너 */
.kurly-footer {
  width: 100%;
  border-top: 1px solid #f7f7f7;
  background-color: #fff;
  padding-top: 20px;
}

/* 내부 레이아웃 가이드 */
.kurly-footer .inner-wrapper {
  width: 1050px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

/* 상단 섹션 */
.footer-top {
  display: flex;
  justify-content: space-between;
  padding-bottom: 30px;
}

/* --- 좌측: 고객행복센터 --- */
.customer-center {width: 530px;}

.customer-center .title {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 5px;
  color: #333;
}

.phone-info .number {
  font-size: 26px;
  font-weight: 800;
  margin-right: 15px;
  color: #333;
  margin-bottom: 5px;
}

.phone-info .time {
  font-size: 14px;
  color: #333;
}

/* 문의 채널 레이아웃 (그리드 스타일) */
.contact-channels {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 20px;
}

.channel-item {
  display: flex;
  align-items: center;
}

.btn-out-line {
  width: 140px; height: 40px;
  border: 1px solid #e3e3e3;
  border-radius: 3px;
  background: #fff;
  font-size: 12pX; color: #333;
  margin-right: 15px;
  cursor: pointer;
  flex-shrink: 0; /* 버튼 크기 유지 */
}

.channel-item .desc {
  font-size: 12px; color: #999;
  line-height: 1.4;
  margin: 0;
}

/* --- 우측: 회사정보 섹션 --- */
.company-info {width: 500px;}

.footer-menu {
  list-style: none;
  padding: 0; margin: 0 0 25px 0;
  display: flex;
  gap: 10px;
}

.footer-menu li a {
  text-decoration: none;
  font-size: 12px;  color: #333;
}

.company-details {
  font-size: 11px;  color: #999;
  line-height: 1.8;
}

.company-details a {color: #5f0080;}

.sns-icons .icons {
  list-style: none;
  padding: 0;
  display: flex;  gap: 10px;
  margin-top: 20px;
}

.sns-icons .icons img {
  width: 30px;  height: 30px;
  text-align: center;
}

/* --- 중단: 인증 마크 (ISMS, 우리은행) --- */
.footer-middle-container{
  width: 100%;
  border-top: 1px solid #eee;
}
.footer-middle {
  display: flex; justify-content: center;
  gap: 150px;
  padding: 20px 0;
}

.footer-middle > div {
  display: flex;
  align-items: center;
  gap: 10px;
}

.footer-middle img {
  width: 34px;  height: 34px;
}

.footer-middle p {
  font-size: 10px;  color: #999;
  line-height: 1.5;
  margin: 0;
}

/* --- 하단: 공지 및 카피라이트 --- */
.footer-bottom {
  padding-top: 20px;
  text-align: center;
  background-color: #f7f7f7;
  margin-top: 20px; padding: 20px 0;
}

.footer-bottom .notice {
  font-size: 10px;  color: #999;
  margin-bottom: 10px;
}

.footer-bottom .copyright {
  font-size: 12px;  color: #999;
}

/* 맨위로 버튼 */
.kurly-footer button[type='button'] {
  position: fixed;  bottom: 20px; right: 20px;
  width: 60px;  height: 60px; background: #fff;
  font-size: 24px;
  border: 1px solid #ddd;
  border-radius: 50%;
  cursor: pointer;
  display: flex;  justify-content: center;  align-items: center;
  box-shadow: 0 2px 8px rgba(0,0,0,0.1);
}