/* ---------------------------------------- Login ---------------------------------------- */
.login-wrapper {
  display: flex; 
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin: 50px 0 50px;
  padding-bottom: 30px;
}

.login-top, .login-bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 340px;
}

h3 {
  padding: 24px 0 26px;
  font-size: 20px;
  font-weight: 700;
}

/* 입력창 스타일 */
.login-top input {
  width: 100%; height: 54px;
  padding: 0 19px;
  border: 1px solid #ccc; border-radius: 4px;
  margin-bottom: 10px;
  font-size: 14px;
  box-sizing: border-box;
}

/* 버튼 스타일 */
.login-top button, .login-bottom button {
  width: 100%; height: 54px;
  border-radius: 4px;
  font-size: 16px; font-weight: 500;
  margin-bottom: 10px;
  display: flex;  justify-content: center;
  align-items: center;
  cursor: pointer;
  gap: 10px;
}

/* 로그인 버튼 */
.login-top button:nth-of-type(1) {
  background-color: #5f0080;
  color: #fff;
  border: none;
}

/* 회원가입 버튼 */
.login-top button:nth-of-type(2) {
  background-color: #fff;
  color: #5f0080;
  border: 1px solid #5f0080;
}

/* 네이버 버튼 */
.login-bottom button:nth-of-type(1) {
  background-color: #03c75a;
  color: #fff;
  border: none;
}

/* 카카오 버튼 */
.login-bottom button:nth-of-type(2) {
  background-color: #fee500;
  color: #191919;
  border: none;
}